import { Helmet } from 'react-helmet-async';
import PropTypes from 'prop-types';

const JobMeta = ({ job }) => {
  if (!job) return null;

  // Format currency to Indian format
  const formatSalary = (salary) => {
    if (!salary) return 'Not Disclosed';
    return `${salary}+ LPA`;
  };

  // Generate meta description
  const getMetaDescription = () => {
    const salary = formatSalary(job.package);
    const location = job.location || 'Multiple Locations';
    const position = job.jobPosition || job.jobTitle;
    const company = job.companyDetails || job.companyName;
    
    return `${position} job opening at ${company}. Package: ${salary}. Location: ${location}. ${job.jobDescription ? job.jobDescription.slice(0, 150) + '...' : ''}`;
  };

  // Get company logo or default image
  const getImage = () => {
    if (job.image) return job.image;
    if (job.companyLogo) return job.companyLogo;
    return 'https://shekru-labs-india.github.io/heyjob_website/assets/img/logo.png';
  };

  // Get job title
  const getTitle = () => {
    const position = job.jobPosition || job.jobTitle;
    const company = job.companyDetails || job.companyName;
    const salary = formatSalary(job.package);
    return `${position} at ${company} | ${salary} | HeyJob`;
  };

  return (
    <Helmet>
      {/* Primary Meta Tags */}
      <title>{getTitle()}</title>
      <meta name="title" content={getTitle()} />
      <meta name="description" content={getMetaDescription()} />

      {/* Open Graph / Facebook */}
      <meta property="og:type" content="website" />
      <meta property="og:title" content={getTitle()} />
      <meta property="og:description" content={getMetaDescription()} />
      <meta property="og:image" content={getImage()} />
      <meta property="og:site_name" content="HeyJob" />

      {/* Twitter */}
      <meta property="twitter:card" content="summary_large_image" />
      <meta property="twitter:title" content={getTitle()} />
      <meta property="twitter:description" content={getMetaDescription()} />
      <meta property="twitter:image" content={getImage()} />

      {/* Job Posting Schema */}
      <script type="application/ld+json">
        {JSON.stringify({
          "@context": "https://schema.org",
          "@type": "JobPosting",
          "title": job.jobPosition || job.jobTitle,
          "description": job.jobDescription || getMetaDescription(),
          "datePosted": job.createdAt ? new Date(job.createdAt.seconds * 1000).toISOString() : new Date().toISOString(),
          "hiringOrganization": {
            "@type": "Organization",
            "name": job.companyDetails || job.companyName,
            "logo": getImage()
          },
          "jobLocation": {
            "@type": "Place",
            "address": {
              "@type": "PostalAddress",
              "addressLocality": job.location,
              "addressCountry": "IN"
            }
          },
          "baseSalary": {
            "@type": "MonetaryAmount",
            "currency": "INR",
            "value": {
              "@type": "QuantitativeValue",
              "value": job.package,
              "unitText": "YEAR"
            }
          },
          "employmentType": job.jobType || "FULL_TIME",
          "industry": job.category || "",
          "occupationalCategory": job.jobPosition || job.jobTitle
        })}
      </script>
    </Helmet>
  );
};

JobMeta.propTypes = {
  job: PropTypes.shape({
    id: PropTypes.string,
    jobTitle: PropTypes.string,
    jobPosition: PropTypes.string,
    companyName: PropTypes.string,
    companyDetails: PropTypes.string,
    companyLogo: PropTypes.string,
    image: PropTypes.string,
    location: PropTypes.string,
    package: PropTypes.number,
    jobType: PropTypes.string,
    jobDescription: PropTypes.string,
    category: PropTypes.string,
    createdAt: PropTypes.shape({
      seconds: PropTypes.number
    })
  }).isRequired
};

export default JobMeta; 